$sectionHeaderPadding: 30px;
$gutterSize: 24px;
$mobileBreakPoint: 767px;

h1.is-style-section-header,
h2.is-style-section-header,
h3.is-style-section-header,
h4.is-style-section-header,
h5.is-style-section-header,
h6.is-style-section-header,
h1[data-type="core/heading"].is-style-section-header,
h2[data-type="core/heading"].is-style-section-header,
h3[data-type="core/heading"].is-style-section-header,
h4[data-type="core/heading"].is-style-section-header,
h5[data-type="core/heading"].is-style-section-header,
h6[data-type="core/heading"].is-style-section-header {
	border-bottom: 1px solid black;
	padding-bottom: $gutterSize;
}

.ui.has-section-header.row,
.ui.has-section-header.grid {
	@media only screen and (min-width: $mobileBreakPoint) {
		padding-top: $sectionHeaderPadding;
	}
	.column {
		@media only screen and (max-width: $mobileBreakPoint) {
			padding-top: $sectionHeaderPadding;
		}
	}
	.column > .is-style-section-header:first-of-type {
		margin-top: -$sectionHeaderPadding;
		margin-bottom: $gutterSize;
	}
}